import commonList from '@/components/common/common-list.vue';
import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
import loadMore from '@/components/common/load-more.vue';
import hotCate from '@/components/news/hot-cate.vue';
import uniSearchBar from '@/components/uni-ui/uni-search-bar/uni-search-bar.vue';
import topicList from '@/components/news/topic-list.vue';

export default {
	components: {
		uniNavBar,
		commonList,
		loadMore,
		uniSearchBar,
		hotCate,
		topicList
	},
	data() {
		return {
			hotCate: [{
				name: '关注',
			}, {
				name: '推荐',
			}, {
				name: '体育',
			}, {
				name: '热点',
			}, {
				name: '财经',
			}, {
				name: '娱乐',
			}],
			loadMore: '上拉加载更多...',
			newList: [{
					username: "昵称1",
					userpic: "/static/default.jpg",
					newstime: "2020-05-24 上午 11：31",
					isFollow: true,
					title: "这是一个标题",
					titlepic: "/static/demo/banner2.jpg",
					support: {
						type: "support",
						support_count: 1,
						unsupport_count: 5
					},
					comment_count: 4,
					share_num: 2
				},
				{
					username: "昵称2",
					userpic: "/static/default.jpg",
					newstime: "2020-05-24 上午 11：31",
					isFollow: true,
					title: "这是一个标题",
					titlepic: "",
					support: {
						type: "unsupport",
						support_count: 1,
						unsupport_count: 5
					},
					comment_count: 4,
					share_num: 0
				},
				{
					username: "昵称1",
					userpic: "/static/default.jpg",
					newstime: "2020-05-24 上午 11：31",
					isFollow: true,
					title: "这是一个标题",
					titlepic: "/static/demo/banner2.jpg",
					support: {
						type: "",
						support_count: 0,
						unsupport_count: 0
					},
					comment_count: 4,
					share_num: 2
				}
			],
			scrollH: 700,
			content: '',
			tabIndex: 0,
			isActive: false,
			tabBars: [{
				name: '关注'
			}, {

				name: '话题'
			}],
			topicList: [{
				cover: "/static/demo/topicpic/1.jpeg",
				title: "话题名称",
				desc: "话题描述",
				today_count: 10,
				news_count: 0
			}, {
				cover: "/static/demo/topicpic/1.jpeg",
				title: "话题名称",
				desc: "话题描述",
				today_count: 10,
				news_count: 0
			}, {
				cover: "/static/demo/topicpic/1.jpeg",
				title: "话题名称",
				desc: "话题描述",
				today_count: 10,
				news_count: 0
			}]
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success: res => {
				this.scrollH = res.windowHeight - res.statusBarHeight - 44;
				console.log(this.scrollH)
			}
		})
	},
	methods: {
		openMore() {
			uni.navigateTo({
				url:"../../pages/topic-nav/topic-nav"
			})
		},
		onOpenRightEvent() {
			uni.navigateTo({
				url: "../add-input/add-input"
			})
		},
		changeTab(e) {
			this.tabIndex = e
		},
		// 监听滑动
		onChangeTab(e) {
			this.changeTab(e.detail.current)
		},
		doSupport(e) {
			let item = this.newList[e.index]
			console.log()
			let msg = e.type === "support" ? "点赞" : "反对";
			if (item.support.type === '') {
				item.support.type = e.type;
				item.support[e.type + '_count']++;
			}
			if (item.support.type === 'support' && e.type === 'unsupport') {
				item.support.support_count--;
				item.support.unsupport_count++;
			} else if (item.support.type === 'unsupport' && e.type === 'support') {
				item.support.support_count++;
				item.support.unsupport_count--;
			}
			item.support.type = e.type;
			uni.showToast({
				title: msg + "成功"
			})
		},
		loadmoreEvent() {
			//数据验证
			if (this.loadMore !== "上拉加载更多...") {
				return;
			}
			this.loadMore = "加载中...";
			setTimeout(() => {
				this.newList = [...this.newList, ...this.newList];
				this.loadMore = "上拉加载更多...";
			}, 2000)
		}
	}
}
